import * as React from "react";
import { Flex, Text, Image, Center } from "@chakra-ui/react";
import { useCityInfo } from "../../context/CityInfoContext.js";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";

export default function AboutCity() {
	const cityInfo = useCityInfo();

	return (
		// Banner below heading showing image, name and description for each game in parallel
		<Flex
			boxSizing="border-box"
			align={"center"}
			justify={"center"}
			direction={"column"}
			p="2"
			bg="black"
			maxW={["100vw", null, "100vw"]}
		>
			<Text
				color={"white"}
				fontSize={"4xl"}
				fontWeight={"semibold"}
				marginBottom={"30"}
			>
				The World
			</Text>
			<Carousel infiniteLoop width={"75vw"}>
				{
					//dynamicHeight, what a shame it doesn't work
				}
				{cityInfo.map((subject) => (
					<Flex
						justify={"center"}
						align={"center"}
						direction={["column", null, null, "row"]}
						gap={"20"}
					>
						<Flex
							justify={"center"}
							align={"center"}
							direction={"column"}
							maxW={["80vw", "55vw", "45vw", "32vw"]}
							gap={"5"}
						>
							<Text color={"white"} fontSize={"2xl"} fontWeight={"semibold"}>
								{subject.name}
							</Text>
							<Text color={"white"} fontSize={"xl"} fontWeight={"semibold"}>
								{subject.d1}
							</Text>
							<Text color={"white"} fontSize={"xl"} fontWeight={"semibold"}>
								{subject.d2}
							</Text>
							<Text color={"white"} fontSize={"xl"} fontWeight={"semibold"}>
								{subject.d3}
							</Text>
							<Text color={"white"} fontSize={"xl"} fontWeight={"semibold"}>
								{subject.d4}
							</Text>
						</Flex>
						<Image
							maxW={["80vw", "55vw", "45vw", "30vw"]}
							src={subject.image}
						/>
					</Flex>
				))}
			</Carousel>
		</Flex>
	);
}
